class DisconectDetector {
    constructor(video){
        this.onPlaying = this.onPlaying.bind(this);
        this.onWaiting = this.onWaiting.bind(this);
        this.onPause = this.onPause.bind(this);
        this.onTimeout = this.onTimeout.bind(this); 

        video.addEventListener("playing", this.onPlaying);
        video.addEventListener("waiting", this.onWaiting);
        video.addEventListener("pause",this.onPause);
    }

    destroy(){
        video.removeEventListener("playing", this.onPlaying);
        video.removeEventListener("waiting", this.onWaiting);
        video.removeEventListener("pause",this.onPause);
        clearTimeout( this.timeout );
    }

    onPlaying(e){
        document.body.style.background="green";
        clearTimeout( this.timeout );
        console.log("onplaying");
    }

    onWaiting(e){
        clearTimeout( this.timeout ); //TODO есть ли повторные отработки waiting ?
        this.timeout = setTimeout(this.onTimeout, 3000);
        console.log("onwaiting");        
    }

    onPause(e){
        clearTimeout( this.timeout );
        console.log("onpause");         
    }

    onTimeout(){
        document.body.style.background="red";
    }
}


export default DisconectDetector;


