
class NativePlayer {

    constructor () {
        alert("init native player");
        var wrapper =document.getElementById("sldp_player_wrapper");
        this.video = document.createElement("video");
        this.video.src = "http://10.0.140.41:8081/live/test/playlist.m3u8";
        this.video.style.border="1px solid black";
        this.video.style.width="100%";
        this.video.style.height="100%";
        this.video.autoplay = "autoplay";
        this.video.controls="controls";
        
        //var source =  document.createElement("source");
        //source.src = "http://10.0.140.41:8081/live/test/playlist.m3u8";
        //source.type="vnd.apple.mpegURL";
        //this.video.appendChild(source);
        //this.video.addEventListener('canplaythrough', () => {})
        wrapper.appendChild( this.video );
    }

    destroy(room){

    }

    static isSupported(){
        return true;
    }

}

export default NativePlayer;


