class Draggable {

    constructor(target){
        this.target = target;
        this.onDragMove = this.onDragMove.bind(this); 
        //this.onDragEnd = this.onDragEnd.bind(this);
        this.onTouchEnd = this.onTouchEnd.bind(this);
        this.onTouchMove = this.onTouchMove.bind(this);
        this.onClick = this.onClick.bind(this);

        var video = this.target.getElementsByTagName("video")[0];

        video.addEventListener("mousedown", this.onDragStart.bind(this));
        video.addEventListener("touchstart", this.onTouchStart.bind(this));
        this.isMove = false;
    }

    //TOUCH
    onTouchStart(e) {
        this.target.addEventListener("touchmove", this.onTouchMove);
        this.target.addEventListener("touchend", this.onTouchEnd);

        var touch = e.changedTouches[0];
        this.startX = touch.screenX; 
        this.startY = touch.screenY;
    }
    

    onTouchMove(e){
        this.isMove = true;
        var touch = e.changedTouches[0];
        
        var top = touch.screenY - this.startY;
        var left =  touch.screenX - this.startX;
        this.setPosition(left, top);

       
        e.preventDefault();
        e.stopPropagation();
       // console.log((rect.top + (this.startX - e.clientX)) + "px");    

        this.startX = touch.screenX; 
        this.startY = touch.screenY;
    }

    onTouchEnd (e) {
        this.isMove = false;
        console.log("mouseup");
        document.removeEventListener("mousemove", this.onTouchMove);
        document.removeEventListener("mouseup", this.onTouchEnd);
    }    

    //MOUSE
    onDragStart (e) {
        document.addEventListener("mousemove", this.onDragMove);
        //document.addEventListener("mouseup", this.onDragEnd);
        document.addEventListener("click", this.onClick, true);

        this.startX = e.clientX; 
        this.startY = e.clientY; 
    }

    onDragMove (e) {
        var topOffset = e.clientY - this.startY,
            leftOffset =  e.clientX - this.startX;     
            
        this.setPosition(leftOffset, topOffset);    

        this.startX = e.clientX; 
        this.startY = e.clientY;
        this.isMove=true;
    }

 
    onClick(e){
        document.removeEventListener("mousemove", this.onDragMove);
        document.removeEventListener("mouseup", this.onDragEnd);
        document.removeEventListener("mouseup", this.onClick);

        if(this.isMove){
            e.preventDefault();
            e.stopPropagation();    
        }
        this.isMove=false;
    }

    setPosition(left, top){
        var docRect = document.body.getBoundingClientRect(),
            rect = this.target.getBoundingClientRect();

        top = top + rect.top;
        left = left + rect.left;

        top = top>0?top:0;
        left = left>0?left:0;
         // TODO заблокировать перемещение вправо

        this.target.style.top = top + "px";
        this.target.style.left = left + "px";
    }
}

export default Draggable;


