import support from "./support.js";
import Draggable from  "./draggable.js";
import DisconnectDetector from  "./DisconnectDetector.js";

import HLSPlayer from  "./players/hls/HLSPlayer.js";
import SLDPPlayer from  "./players/sldp/SLDPPlayer.js";
import NativePlayer from  "./players/native/NativePlayer.js";
import agent from  "./Browser.js";

class WhenspeakStreamer {

    constructor(params){

        params = params ||{};
        this.params = params;

        this.wrapper = document.createElement("div");
        this.wrapper.id ="sldp_player_wrapper";
        this.wrapper.style.width = params.width || "400px";
        this.wrapper.style.height = params.height || "270px";
        this.wrapper.style.background = "black";
        this.wrapper.style.position = "absolute";

        document.body.appendChild( this.wrapper );
        this.detectorOnPlaying = this.detectorOnPlaying.bind(this);
        this.play(5);
    }

    // НА IOS запускаем нативный, если он не играет то врубаем флэшфонер
    // 
    createPlayer( room ){
        var is_safari = navigator.userAgent.toLowerCase().indexOf('safari/') > -1;
       // var isSafari = !!navigator.userAgent.match(/Version\/[\d\.]+.*Safari/);

        alert(agent);

        if ((navigator.userAgent.match(/iPad/i) || navigator.userAgent.match(/iPhone/i)) && is_safari) {   
            return new NativePlayer(room, this.params);         
        } else {
            try{
                return new SLDPPlayer(room, this.params);  
            } catch (e){
                return new HLSPlayer(room, this.params);
            }
        }
    }

    play (roomId) {
        if(this.currentRoom !==  roomId){
            this.currentRoom = roomId;
            this.instance = this.createPlayer();
            if(this.instance){
               //setTimeout(this.setSize.bind(this), 5000);
                new Draggable(this.wrapper);
                this.detector();
            }
        }
    }

    stop (){
        this.instance.destroy();
        this.instance = null;
        this.currentRoom = null;
        clearTimeout(this.timeout);
        if(this.video){
           video.removeEventListener("timeupdate", this.detectorOnPlaying) 
        }
    }

    detector(){
        var video = this.video = this.wrapper.getElementsByTagName("video")[0];
        var detector = new DisconnectDetector(video);
    }

    detectorOnPlaying(){
        this.time = Date.now();
        document.body.style.background = "green";
        //console.log("detectorOnPlaying");
    }
}

window.WhenSpeakStreamer = WhenspeakStreamer;


